import { Component, OnInit, AfterViewInit } from '@angular/core';
import bulmaCalendar from 'bulma-calendar/dist/js/bulma-calendar.min.js';
import bulmaCollapsible from '@creativebulma/bulma-collapsible';
import { Subject } from 'rxjs/internal/Subject';
import { environment } from '../../environments/environment';
import { FormSubmissionService } from './form-submission.service';
import { Observable, throwError } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-contact-modal',
  templateUrl: './contact-modal.component.html',
  styleUrls: ['./contact-modal.component.sass']
})
export class ContactModalComponent implements OnInit, AfterViewInit {

  /**
   * handles whats shown inside the modal
   */
  modalStage = 'menu';
  /**
   * possible values for modalstage
   */
  modalStages = ['menu', 'callback', 'appointment'];
  /**
   * handles weather the modal is displayed or not
   */
  showModal = false;
  weekdays = ['Montag', 'Dienstag', 'Mittwoch', 'Donnerstag', 'Freitag'];
  contentElements = [];
  arrowIcons = [];
  currentlyOpenedElement = undefined;
  /**
   *  stores active subscriptions
   */
  private ngUnsubscribe: Subject<void> = new Subject<void>();
  appointmentFormData = {
    info: {
      name: '',
      phone: '',
      email: '',
      date: '',
      time: '',
      context: '',
    },
    sendTo: environment.mailTo
  };

  constructor(private formSubmissionService: FormSubmissionService) {}

  ngOnInit(): void {



  }

  sendMail(event): void {
    console.log('FormSubmitEvent: ', event);
    console.log('appointmentFormData: ', this.appointmentFormData);
    console.log('Sending Email Request...');
    this.formSubmissionService.sendFormAsEmail(this.appointmentFormData)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe((res: any) => {
          console.log('RES: ', res);
        }, (error) => {
          console.log('ERROR: ', error);
        });
  }

  ngAfterViewInit(): void {}

  adjustModalHeight(): void {
    const modalBodyElement = document.getElementById('modalbody');

    if (this.modalStage === 'menu') {
      modalBodyElement.classList.remove('modal-body-height-100');
      modalBodyElement.classList.add('modal-body-height-50');
    } else {
      modalBodyElement.classList.remove('modal-body-height-50');
      modalBodyElement.classList.add('modal-body-height-100');
    }
  }

  navigate(nav: string): void {
    this.modalStage = nav;
    this.adjustModalHeight();
    if (this.modalStage === 'callback') {
      setTimeout(() => {
        this.initializeDatePickerComponent();
        this.initializeCollapsableComponent();
        this.getContentElements();
      }, 1);
    }
  }

  getContentElements(): void {
    this.contentElements = (document.getElementsByClassName('contentfield') as any);
    this.arrowIcons = (document.getElementsByClassName('arrow-icon') as any);
  }

  initializeCollapsableComponent(): void {

    const options = {
      allowMultiple: false
    };

    const bulmaCollapsibleInstances = [];

    this.weekdays.forEach((weekday, index) => {
      let classString = 'is-collapsible';
      classString += ('-' + index);

      // const instance = bulmaCollapsible.attach('.' + classString, options)[0];
      // bulmaCollapsibleInstances.push(instance);
      bulmaCollapsibleInstances.push(bulmaCollapsible.attach('.' + classString, options)[0]);
    });

    bulmaCollapsibleInstances.forEach((bulmaCollapsibleInstance, i) => {
      // collapse all elements on init
      bulmaCollapsibleInstance.collapse();
    });

  }

  initializeDatePickerComponent(): void {
    const options = {
      type: 'time',
      color: '#6ba7ff',
      isRange: true,
      displayMode: 'dialog'
    };

    const timePickers = [];

    this.weekdays.forEach((weekday, index) => {

      let classString = 'datepicker-instance';
      classString += ('-' + index);
      const datePickerInstance = document.getElementsByClassName(classString)[0];
      // console.log('datePickerInstance', datePickerInstance);
      timePickers.push(bulmaCalendar.attach(datePickerInstance)[0]);
    });

    console.log('timepickers: ', timePickers);

    // // Initialize all input of date type.
    // const calendars = bulmaCalendar.attach('[type="date"]', options);
    //
    // // Loop on each calendar initialized
    // calendars.forEach(calendar => {
    //   // Add listener to select event
    //   calendar.on('select', date => {
    //     console.log('Date selected: ', date);
    //   });
    // });
  }

  // manually hide or show the content area
  collapseableTrigger(index: number): void {

    /**
     * TODO: dafür sorgen dass beim öffnen eines dropdown, das bereits offene geschlossen wird
     */
    // if (this.currentlyOpenedElement) {
    //   this.triggerContentHiding(this.currentlyOpenedElement);
    //   this.triggerArrowIcons(this.currentlyOpenedElement);
    // }

    this.triggerContentHiding(index);
    this.triggerArrowIcons(index);

  }

  triggerArrowIcons(index: number): void {
    if (this.arrowIcons[index].classList.contains('down-arrow-icon')) {
      this.arrowIcons[index].classList.add('up-arrow-icon');
      this.arrowIcons[index].classList.remove('down-arrow-icon');
      this.currentlyOpenedElement = index;
    } else if (this.arrowIcons[index].classList.contains('up-arrow-icon')) {
      this.arrowIcons[index].classList.add('down-arrow-icon');
      this.arrowIcons[index].classList.remove('up-arrow-icon');
      this.currentlyOpenedElement = undefined;
    }
  }

  triggerContentHiding(index: number): void {
    if (this.contentElements[index].classList.contains('hide')) {
      this.contentElements[index].classList.remove('hide');
      this.currentlyOpenedElement = index;
    } else {
      this.contentElements[index].classList.add('hide');
      this.currentlyOpenedElement = undefined;
    }
  }

  toggleModal(): void {
    this.modalStage = 'menu';
    this.showModal = !this.showModal;
  }
}
